.product-card-details {
  display: flex;
  gap: 8px;
  width: 100%;
  justify-content: space-between;

  &__name {
    color: $gray;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }

  &__value {
    color: $gray-close-black;
    text-align: right;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }
}
